#sTooltip {
  z-index: 1000;
  opacity: 0;
  width: 220px;
  height: 0;
  pointer-events: none;
  transition: opacity .3s ease-out;
  position: absolute;
  transform: translate(-50%);
}

#sTooltip--content {
  width: 100%;
  height: auto;
  color: #c6c1b7;
  transform: translateX(calc(var(--mouse-x) * -1%));
  background-color: #302946;
  border-radius: 2px;
  margin-bottom: .6rem;
  padding: 1rem 1.5rem;
  transition: background-color .3s ease-out;
  position: absolute;
  bottom: 100%;
  left: 50%;
  overflow: hidden;
}

#sTooltip--content.pp {
  color: #302946;
}

#sTooltip--content.pp--501 {
  background-color: #f3d852;
}

