  background-color: #9aa2c7;
}

#sTooltip--mark {
  width: 1rem;
  height: .6rem;
  transform: translateX(calc(var(--mouse-x) * -1%));
  border-top: .6rem solid #302946;
  border-left: .5rem solid #0000;
  border-right: .5rem solid #0000;
  transition: border-top-color .3s ease-out;
  position: absolute;
  bottom: 100%;
  left: 50%;
}

