  display: flex;
}

.switch--button {
  cursor: pointer;
  height: 1rem;
  width: 1.8rem;
  background: #638cbb;
  border-radius: 100px;
  align-items: center;
  margin: 0;
  padding: 0;
  transition: background .32s;
  display: inline-flex;
  position: relative;
}

.switch--button:after {
  content: "";
  pointer-events: none;
  height: .666667rem;
  width: .666667rem;
  background: #c6c1b7;
  border-radius: 100px;
  transition: left .32s cubic-bezier(.68, -.55, .27, 1.55);
  display: block;
  position: absolute;
  top: .166667rem;
  left: .166667rem;
}

.switch--button[aria-checked="true"] {
  background: #c7095b;
}

.switch--button[aria-checked="true"]:after {
  left: calc(100% - .833333rem);
}

